@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600&display=swap');

:root {
    --gap-xxs: 4px;
    --gap-xs: 8px;
    --gap-s: 12px;
    --gap-m: 16px;

    --border-radius-s: 8px;
    --border-radius-l: 16px;

    --text: #333;
    --text-secondary: #666;

    --bg: #fff;
    --bg-0: #ffffff00;
    --bg-secondary: #eee;

    --custom-color: #0054ff2b;
}

.voice {
    height: 20px;
    width: 20px;
}

body {
    font-family: 'Nunito Sans', sans-serif;
    margin: 0;
    padding: 0;
    color: var(--text);
    background: var(--bg-secondary);
}

* {
    box-sizing: border-box;
}

button {
    font-family: inherit;
    font-size: inherit;
    background: none;
    border: none;
    padding: 0;
}

.root {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: var(--gap-xs);
    padding-bottom: 0;
}

.head {
    display: flex;
    justify-content: space-between;
    padding: var(--gap-m);
    font-size: 18px;
}

.title {
    text-align: center;
    width: 100%;
    font-size: 2em;
    margin: 0;
}

.current {
    flex-grow: 1;
    display: flex;
    overflow: hidden;
    position: relative;
}

.current > * {
    width: 100%;
    margin: 0 var(--gap-xs);

    @media (max-width: 720px) {
        position: absolute;
    }
}

.hidden {
    display: flex;

    @media (max-width: 720px) {
        display: none;
    }
}

.btn {
    color: var(--text-secondary);
}

.btnSel {
    color: var(--custom-color);
}