.root {
    padding: var(--gap-m);
    background: var(--bg);
    border-radius: var(--border-radius-l);
    overflow: auto;
    height: 100%;
}

.items {
    display: flex;
    flex-direction: column;
    min-height: 100%;

}

.items > *:first-child {
    margin-top: auto;
}

.from, .to {
    max-width: 85%;
    width: fit-content;
    display: flex;
    align-items: flex-end;
    margin-top: var(--gap-xs);
}

.from {
    margin-left: auto;
}

.body {
    position: relative;
    display: inline-flex;
    flex-direction: column;
    padding: var(--gap-s);
    border-radius: var(--border-radius-l);
    color: var(--text-secondary);
    background: var(--bg-secondary);

    &:not(:last-child){
         margin-bottom: var(--gap-s);
     }
}

.from .body {
    background: var(--bg-primary);
    border-bottom-right-radius: 0;
}

.to .body {
    border-bottom-left-radius: 0;
}

.user {
    position: relative;
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    background: var(--bg-secondary);
    margin-right: var(--gap-xs);
}

.online {
    background: var(--custom-color);
    border: 2px solid var(--bg);
    position: absolute;
    right: 0;
    bottom: 0;
    height: 20%;
    width: 20%;
    border-radius: 50%;
}

.userName {
    font-weight: 600;
    /*width: 100%;*/
}